import { PrismaClient } from '@prisma/client';
import { PrismaNeon } from '@prisma/adapter-neon';
import { Pool } from '@neondatabase/serverless';

export interface Env {
  DATABASE_URL: string;
  JWT_SECRET: string;
  RAZORPAY_KEY_ID: string;
  RAZORPAY_KEY_SECRET: string;
  RAZORPAY_WEBHOOK_SECRET: string;
  WHATSAPP_TOKEN: string;
  WHATSAPP_PHONE_NUMBER_ID: string;
  WHATSAPP_TEMPLATE_NAME: string;
  APP_BASE_URL: string;
}

export function getPrisma(env: Env) {
  // Create Pool with connection string
  // The Pool handles connection pooling automatically
  const pool = new Pool(env.DATABASE_URL);
  
  // Create the Neon adapter with the pool
  const adapter = new PrismaNeon(pool);
  
  // Create and return Prisma Client with the adapter
  return new PrismaClient({ adapter });
}

